<template>
  <div id="top-bar">
    <el-form :inline="true" class="demo-form-inline">
      <!--设备选择器-->
      <el-form-item label="设备">
        <el-select v-model="deviceSerial" placeholder="选择监控设备设备"
                   @change="chooseCamera(deviceSerial)">
          <el-option v-for="(item,i) in deviceList" :key="i" :value="item.deviceSerial">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="在线状态">
        <el-tag :type="this.deviceList[deviceArrayNo].status === 1 ?'':'danger'">
          {{ this.deviceList[deviceArrayNo].status === 1 ? '在线' : '离线' }}
        </el-tag>
      </el-form-item>
      <el-form-item label="设备版本号">
        <el-tag>{{ this.deviceList[this.deviceArrayNo].deviceVersion }}</el-tag>
      </el-form-item>
      <el-form-item label="风险等级">
        <el-tag :type="this.deviceList[deviceArrayNo].riskLevel === 0 ?'':'danger'">
          {{ this.deviceList[deviceArrayNo].riskLevel === 0 ? '安全' : '危险' }}
        </el-tag>
      </el-form-item>
      <el-form-item label="IP地址">
        <el-tag>{{ this.deviceList[deviceArrayNo].netAddress }}</el-tag>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'top-bar',
  data() {
    return {
      // 先初始化一个，后面获取的会覆盖这个初始化的
      deviceList: [
        {
          'id': 'c81e36f3c1a84659be1910585baeacb3J03807958',
          'deviceSerial': 'J03807958',
          'deviceName': 'DS-2DE4223IW-D/GLT/XM(J03807958)',
          'deviceType': 'DS-2DE4223IW-D/GLT/XM',
          'status': 1,
          'defence': 0,
          'deviceVersion': 'V5.7.1 build 211015',
          'addTime': 1636438446370,
          'updateTime': 1659183152000,
          'parentCategory': 'COMMON',
          'riskLevel': 0,
          'netAddress': '203.168.21.105'
        }
      ],
      deviceSerial: 'J03807958',
      deviceArrayNo: 0
    }
  },
  mounted() {
    window.setDeviceList = this.setDeviceList
  },
  methods: {
    setDeviceList(deviceList) {
      this.deviceList = deviceList
    },
    chooseCamera(deviceSerial) {
      // 判断第几个数组
      for (let i = 0; i < this.deviceList.length; i++) {
        if (this.deviceList[i].deviceSerial = deviceSerial) {
          this.deviceArrayNo = i
        }
      }
      this.deviceSerial = deviceSerial
      this.$emit('chooseCamera', deviceSerial)
    }
  }
}
</script>

<style scoped>
#top-bar {
  display: flex;
  justify-content: center;
}
</style>



