import axios from 'axios'

const monitor = {
  state: {
    accessToken: '',
    deviceList: []
  },

  mutations: {
    SET_AccessToken: (state, accessToken) => {
      state.accessToken = accessToken
    },
    SET_DeviceList: (state, deviceList) => {
      // 给topBar中的deviceList赋初值
      setDeviceList(deviceList)
      state.deviceList = deviceList
    }
  },
  actions: {
    // 获取AccessToken
    AccessToken({ dispatch, commit }) {
      return new Promise((resolve, reject) => {
        axios({
          url: 'https://open.ys7.com/api/lapp/token/get',
          params: {
            appKey: '22ec17f6452a48b7a6a992c027d7da59',
            appSecret: '4c4e2fd767513bfef9ca96e9d8ec38c0'
          },
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          method: 'POST'
        }).then(res => {
          commit('SET_AccessToken', res.data.data.accessToken)
          dispatch('DeviceList', res.data.data.accessToken)
          resolve(res)
        }).catch(error => {
          reject(error)
        })
      })
    },
    DeviceList({ dispatch, commit }, accessToken) {
      return new Promise((resolve, reject) => {
        axios({
          url: 'https://open.ys7.com/api/lapp/device/list',
          params: {
            accessToken: accessToken
          },
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          method: 'POST'
        }).then(res => {
          commit('SET_DeviceList', res.data.data)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    ControlCamera({ dispatch, commit }, info) {
      return new Promise((resolve, reject) => {
        axios({
          url: 'https://open.ys7.com/api/lapp/device/ptz/start',
          params: {
            accessToken: info.accessToken,
            deviceSerial: info.deviceSerial,
            direction: info.direction,
            channelNo: info.channelNo,
            speed: info.speed
          },
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          method: 'POST'
        }).then(response => {
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    },
    StopCamera({ dispatch, commit }, info) {
      return new Promise((resolve, reject) => {
        axios({
          url: 'https://open.ys7.com/api/lapp/device/ptz/stop',
          params: {
            accessToken: info.accessToken,
            deviceSerial: info.deviceSerial,
            channelNo: info.channelNo
          },
          header: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          method: 'POST'
        }).then(response => {
          if (info.direction != 12) dispatch('ControlCamera', info)
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })

    }
  }
}

export default monitor



