<template>
  <div id="bottom-button">
    <el-row id="el-row">
      <el-col>
        <el-button icon="my-icon-amplification" circle @click="controlCamera(8)">放大</el-button>
      </el-col>
    </el-row>
    <el-row id="el-row">
      <el-col>
        <el-button icon="my-icon-left-up" circle @click="controlCamera(4)">左上</el-button>
        <el-button icon="my-icon-up" circle @click="controlCamera(0)">上</el-button>
        <el-button icon="my-icon-right-up" circle @click="controlCamera(6)">右上</el-button>
      </el-col>
    </el-row>
    <el-row id="el-row">
      <el-button icon="my-icon-close-focus" circle @click="controlCamera(10)">近焦</el-button>
      <el-button icon="my-icon-left" circle @click="controlCamera(2)">左</el-button>
      <el-button icon="my-icon-stop" circle @click="controlCamera(12)">停</el-button>
      <el-button icon="my-icon-right" circle @click="controlCamera(3)">右</el-button>
      <el-button icon="my-icon-distant-focus" circle @click="controlCamera(11)">远焦</el-button>
    </el-row>
    <el-row id="el-row">
      <el-col>
        <el-button icon="my-icon-left-down" circle @click="controlCamera(5)">左下</el-button>
        <el-button icon="my-icon-down" circle @click="controlCamera(1)">下</el-button>
        <el-button icon="my-icon-right-down" circle @click="controlCamera(7)">右下</el-button>
      </el-col>
    </el-row>
    <el-row id="el-row">
      <el-col>
        <el-button icon="my-icon-scales" circle @click="controlCamera(9)">缩小</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'bottom',
  data() {
    return {}
  },
  methods: {
    controlCamera(command) {
      this.$emit('controlCamera', command)
    }
  }
}
</script>

<style lang="scss" scoped>
#el-row {
  line-height: 50px;
}
</style>



