<template>
  <div id="app">
    <div>
      <top-bar ref="topBar" @chooseCamera="chooseCamera"></top-bar>
    </div>
    <div id="video-container"></div>
    <div id="bottom" style="margin-top: 10px">
      <bottom @controlCamera="controlCamera"></bottom>
    </div>
  </div>
</template>

<script>
import EZUIKit from 'ezuikit-js'
import TopBar from './components/top-bar'
import Bottom from './components/bottom'

export default {
  name: 'orchard-monitor',
  components: {
    TopBar,
    Bottom
  },
  data() {
    return {
      player: null,
      accessToken: '',
      deviceSerial: 'J03807958',
      play: false,

      windowWidth: document.documentElement.clientWidth, //获取屏幕宽度
      windowHeight: document.documentElement.clientHeight, //获取屏幕高度
    }
  },
  mounted() {
    this.getAccessToken()
    let that = this;

    window.onresize = () => {

      return (() => {
        window.fullHeight = document.documentElement.clientHeight;
        window.fullWidth = document.documentElement.clientWidth;

        that.windowHeight = window.fullHeight; //获取屏幕高度
        that.windowWidth = window.fullWidth; //获取屏幕宽度

      })()

    };
  },
  beforeDestroy() {
    document.getElementById('video-container').innerHTML = ''
  },
  watch: {
    windowHeight(val) {
      let that = this;
      console.log("实时屏幕高度：", val, that.windowHeight);

    },

    windowWidth(val) {

      let that = this;
      console.log("实时屏幕宽度：", val, that.windowHeight);

    }
  },
  methods: {
    initPlayer() {
      let that = this
      if (this.player === null) {
        this.player = new EZUIKit.EZUIKitPlayer({
          id: 'video-container', // 视频容器ID
          autoplay: true,
          accessToken: this.accessToken,
          url: 'ezopen://open.ys7.com/J03807958/1.hd.live',
          template: 'security',
          splitBasis: 4,
          plugin: ['talk'],
          // width: 390, // 宽度高度为容器规格，将按照窗口数均分
          // height: 220
          width: that.windowWidth,
          height: that.windowWidth * 0.5625
        })
      }
    },
    getAccessToken() {
      this.$store.dispatch('AccessToken').then((res) => {
        this.accessToken = res.data.data.accessToken
      }).then(() => {
        this.initPlayer()
      })
    },
    chooseCamera(deviceSerial) {
      this.deviceSerial = deviceSerial
      this.player.stop()
        .then(() => {
          this.player.play('ezopen://open.ys7.com/' + deviceSerial + '/1.hd.live')
        })
    },
    controlCamera(command) {
      let info = {
        accessToken: this.accessToken,
        deviceSerial: this.deviceSerial,
        channelNo: 1,
        speed: 1,
        direction: command
      }
      this.$store.dispatch('StopCamera', info)
    }
  }
}
</script>

<style scoped>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



